export default function SelectJobType(props) {
    const classes = useStyles();
    const theme = useTheme();

    const onSelectJobType = (e, job_type) => {

        props.onSelectJobType(jobsTemplates[job_type]);
        props.onClose();
    }

    const handleClose = () => {
        //setSelectType(false);
        props.onClose();
    };   

    return (
        <React.Fragment>
            <Dialog fullScreen open={props.open} onClose={e => handleClose(e)} TransitionComponent={Transition}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={e => handleClose(e)} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="subtitle2" className={classes.title}>
                            Новое задание
              </Typography>
                        <IconButton autoFocus color="inherit" disabled={true}>
                            <SaveIcon />
                        </IconButton>
                    </Toolbar>
                </AppBar>
                <Paper square className={classes.root}>
                    <Typography variant="h6">Выберите тип задания</Typography>


                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel1a-content"
                            id="panel1a-header"
                        >
                            <Typography variant="subtitle2">Регулярный опрос команды</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Typography component="div">
                                <Box fontSize="fontSize" m={1}>
                                    Позволяет с указанной вами регулярностью провести опрос в чате участников команды и на основании полученных ответов заполнить указанный вами отчет, а также отправить отчет на эл. почту
             <br />Подходит сбора ежедневных или еженедельных отчетов у сотрудников, таких как, Standup
             </Box>
                            </Typography>

                        </ExpansionPanelDetails>
                        <ExpansionPanelActions>
                            <Button color="primary" onClick={e => onSelectJobType(e, 'standup')}>
                                Создать
            </Button>
                        </ExpansionPanelActions>
                    </ExpansionPanel>
                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel2a-content"
                            id="panel2a-header"
                        >
                            <Typography variant="subtitle2">Сбор и обновление данных</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Typography component="div">
                                <Box fontSize="fontSize" m={1}>
                                    Позволяет с указанной вами регулярностью запросить в чате у участников команды новые данные по строкам из отчета и на основании полученных ответов обновить отчет, а также отправить отчет на эл. почту
             <br />Подходит для регулярного трекинга задач, поручений или целей и подготовки статус-отчета
             </Box>
                            </Typography>
                        </ExpansionPanelDetails>
                        <ExpansionPanelActions>
                            <Button color="primary" onClick={e => onSelectJobType(e, 'update_request')}>
                                Создать
            </Button>
                        </ExpansionPanelActions>
                    </ExpansionPanel>
                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel3a-content"
                            id="panel3a-header"
                        >
                            <Typography variant="subtitle2">Отправка отчета (без опроса)</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Typography component="div">
                                <Box fontSize="fontSize" m={1}>
                                    Позволяет с указанной вами регулярностью отправлять на эл. почту или в чат необходимый вам отчет
             <br />Подходит для отправки сводных отчетов или отчетов, загруженных из внешних систем
             </Box>
                            </Typography>
                        </ExpansionPanelDetails>
                        <ExpansionPanelActions>
                            <Button color="primary" onClick={e => onSelectJobType(e, 'send_report')}>
                                Создать
            </Button>
                        </ExpansionPanelActions>
                    </ExpansionPanel>
                </Paper>
            </Dialog>
        </React.Fragment>
    );
  }

